import { connect } from 'react-redux';

import FeaturedImageCollectionPageConfig from './FeaturedImageCollectionPageConfig';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.featuredImageCollectionPage };
};

const mapDispatchToProps = {
  setParams: setPageParams,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FeaturedImageCollectionPageConfig);
