import imageItem from '../../../mockData/item.json';
import videoItem from '../../../mockData/videoItem.json';
import subscriptions from '../../../mockData/subscriptions.json';
import licenseHistory from '../../../mockData/licenseHistory.json';
import videolicenseHistory from '../../../mockData/videoLicenseHistory.json';
import imageCollectionItems from '../../../mockData/imageCollectionItems.json';
import videoCollectionItems from '../../../mockData/videoCollectionItems.json';

const getPageConfig = (page) => {
  if (page.name === 'searchPage') {
    const onItemClick = page.props.onItemClick
      ? `,
      onItemClick: (e, item) => {
        e.preventDefault();
        console.log(item);
      }`
      : '';
    const overlayAction = page.props.overlayActions
      ? `,
      overlayActions: [
        {
          label: 'Insert preview',
          icon: 'path_to_your_icon',
          onClick: (e, item) => {
            e.preventDefault();
            console.log(item);
          }
        },
      ]`
      : '';
    const searchFilters = page.props.searchFilters
      ? `,
      searchFilters: {
        showFilterDrawer: true,
        images: {
          imageTypeFilter: true,
          orientationFilter: true
        }
      }`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.SearchPage,
    props: {
      mediaType: '${page.props.mediaType}',
      title: '${page.props.widgetTitle}',
      subtitle: '${page.props.widgetSubtitle}',
      dynamicTitle: ${page.props.dynamicTitle},
      dynamicSubtitle: ${page.props.dynamicSubtitle},
      showMore: ${page.props.showMore},
      showSearchBar: ${page.props.showMore}${onItemClick}${overlayAction}${searchFilters}
    }
  }`;
  }

  if (page.name === 'imageDetailsPage') {
    const imageDetailsLicenseButton = page.props.displayButtons
      ? `,
      buttons:[{
        label: 'License',
        isPrimary: true,
        onClick: async (e, item) => {
          e.preventDefault();
          console.log(item);
        },
      }]`
      : '';

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.ImageDetailsPage,
    props: {
      // This is mocked data for image.
      item: ${JSON.stringify(imageItem)}${imageDetailsLicenseButton}
    }
  }`;
  }
  if (page.name === 'imageLicensingPage') {
    const licenseButton = page.props.displayButtons
      ? `,
      buttons: [{
        label: 'License',
        isPrimary: true,
        onClick: async (e, item, props) => {
          e.preventDefault();
          console.log(item);
          console.log(props.subscriptions);
        }
      }]`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.ImageLicensingPage,
    props: {
      // This is mocked data for image.
      item: ${JSON.stringify(imageItem)},
      // This is mocked data for subscription. You will need to implement server side code to get the users subscriptions.
      subscriptions: ${JSON.stringify(subscriptions)}${licenseButton}
    }
  }`;
  }

  if (page.name === 'imageLicenseHistoryPage') {
    const onLicenseHistoryItemClick = page.props.onLicenseHistoryItemClick
      ? `,
      onLicenseHistoryItemClick: (item) => {
        // navigate to the image details when a user clicks on image on license history
        console.log(item);
      },`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.ImageLicenseHistoryPage,
    props: {
      // This is mocked data for license history. You will need to implement server side code to get the license history
      licenseHistory: ${JSON.stringify(
        licenseHistory
      )}${onLicenseHistoryItemClick}
    }
  }`;
  }

  if (page.name === 'videoDetailsPage') {
    const videoDetailsLicenseButton = page.props.displayButtons
      ? `,
      buttons:[{
        label: 'License',
        isPrimary: true,
        onClick: async (e, item) => {
          e.preventDefault();
          console.log(item);
        },
      }]`
      : '';

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.VideoDetailsPage,
    props: {
      // This is mocked data for video.
      item: ${JSON.stringify(videoItem)}${videoDetailsLicenseButton}
    }
  }`;
  }
  if (page.name === 'videoLicensingPage') {
    const licenseButton = page.props.displayButtons
      ? `,
      buttons: [{
        label: 'License',
        isPrimary: true,
        onClick: async (e, item, props) => {
          e.preventDefault();
          console.log(item);
          console.log(props.subscriptions);
        },
      }]`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.VideoLicensingPage,
    props: {
      // This is mocked data for video.
      item: ${JSON.stringify(videoItem)},
      // This is mocked data for subscription. You will need to implement server side code to get the users subscriptions.
      subscriptions: ${JSON.stringify(subscriptions)}${licenseButton}
    }
  }`;
  }

  if (page.name === 'videoLicenseHistoryPage') {
    const onLicenseHistoryItemClick = page.props.onLicenseHistoryItemClick
      ? `,
      onLicenseHistoryItemClick: (item) => {
        // navigate to the video details when a user clicks on video on license history
        console.log(item);
      },`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.VideoLicenseHistoryPage,
    props: {
      // This is mocked data for license history. You will need to implement server side code to get the license history
      licenseHistory: ${JSON.stringify(
        videolicenseHistory
      )}${onLicenseHistoryItemClick}
    }
  }`;
  }

  if (page.name === 'imageCollectionPage') {
    const onImageCollectionItemClick = page.props.onItemClick
      ? `,
      onItemClick: (item) => {
        console.log(item);
      }`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.ImageCollectionPage,
    props: {
      pageTitle: '${page.props.pageTitle}',
      items: ${JSON.stringify(
        imageCollectionItems
      )}${onImageCollectionItemClick},
    }
  }`;
  }

  if (page.name === 'videoCollectionPage') {
    const onVideoCollectionItemClick = page.props.onItemClick
      ? `,
      onItemClick: (item) => {
        console.log(item);
      }`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.VideoCollectionPage,
    props: {
      pageTitle: '${page.props.pageTitle}',
      items: ${JSON.stringify(
        videoCollectionItems
      )}${onVideoCollectionItemClick},
    }
  }`;
  }
  if (page.name === 'featuredImageCollectionPage') {
    const onImageCollectionItemClick = page.props.onItemClick
      ? `,
      onItemClick: (item) => {
        console.log(item);
      },`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.FeaturedImageCollectionPage,
    props: {
      pageTitle: '${page.props.pageTitle}',
      pageSubtitle: '${
        page?.props?.pageSubtitle || ''
      }'${onImageCollectionItemClick}
    }
  }`;
  }

  if (page.name === 'featuredVideoCollectionPage') {
    const onVideoCollectionItemClick = page.props.onItemClick
      ? `,
      onItemClick: (item) => {
        console.log(item);
      },`
      : ``;

    return `{
    name: '${page.name}',
    component: window.ShutterstockWidget.components.FeaturedVideoCollectionPage,
    props: {
      pageTitle: '${page.props.pageTitle}',
      pageSubtitle: '${
        page?.props?.pageSubtitle || ''
      }'${onVideoCollectionItemClick}
    }
  }`;
  }
};

export { getPageConfig };
