import { connect } from 'react-redux';

import ImageCollectionPageConfig from './ImageCollectionPageConfig';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.imageCollectionPage };
};

const mapDispatchToProps = {
  setParams: setPageParams,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ImageCollectionPageConfig);
