import {
  defaultImageDetailsPage,
  defaultImageLicensingPage,
  defaultSearchPage,
  defaultImageLicenseHistoryPage,
  defaultVideoDetailsPage,
  defaultVideoLicensingPage,
  defaultVideoLicenseHistoryPage,
  defaultImageCollectionPage,
  defaultVideoCollectionPage,
  defaultFeaturedImageCollectionPage,
  defaultFeaturedVideoCollectionPage,
} from "./defaultPageConfigs";

const intialState = {
  activePage: "searchPage",
  searchPage: defaultSearchPage,
  imageDetailsPage: defaultImageDetailsPage,
  imageLicensingPage: defaultImageLicensingPage,
  imageLicenseHistoryPage: defaultImageLicenseHistoryPage,
  videoDetailsPage: defaultVideoDetailsPage,
  videoLicensingPage: defaultVideoLicensingPage,
  videoLicenseHistoryPage: defaultVideoLicenseHistoryPage,
  imageCollectionPage: defaultImageCollectionPage,
  videoCollectionPage: defaultVideoCollectionPage,
  featuredImageCollectionPage: defaultFeaturedImageCollectionPage,
  featuredVideoCollectionPage: defaultFeaturedVideoCollectionPage,
};

export default (prevState = { ...intialState }, action) => {
  switch (action.type) {
    case "SET_ACTIVE_PAGE":
      return {
        ...prevState,
        activePage: action.payload.activePage,
      };

    case "SET_PAGES_PARAMS":
      const pageName = action.payload?.name;

      return {
        ...prevState,
        [pageName]: {
          ...prevState?.[pageName],
          props: {
            // ...prevState.props,
            ...action.payload?.props,
          },
        },
      };

    default:
      return prevState;
  }
};
