import React, { useState } from 'react';
import { Button, Form, Collapse } from 'react-bootstrap';

import Checkbox from '../Checkbox';
import Editor from '../Editor';
import PageConfigModal from '../PagesConfig/PageConfigModal';

import classes from '../LeftNavMenu/LeftNavMenu.module.scss';

const defaultButtonVideoDetailsPage = `[
  {
    label: 'License',
    isPrimary: true,
    onClick: async (e, item) => {
      e.preventDefault();
      console.log(item);
    }
  }
]`;


const VideoDetailsPage = ({
  page,
  setParams,
  activated
}) => {
  const [displayButtons, setDisplayButtons] = useState(page.props.displayButtons);

  const [show, setShow] = useState(false);
  return (
    <>
      <div>
        {activated && (
          <a href="#" onClick={(e) => {
            e.preventDefault();
            setShow(true)
          }}> Edit</a>
        )}
      </div>
      <PageConfigModal show={show} setShow={setShow} pageDisplayName="Video Details Page">
        <Form>
          <Checkbox label="Display custom buttons" value="displayButtons" defaultChecked={displayButtons} onChange={(e) => setDisplayButtons(e.target.checked)} />
          <Collapse in={displayButtons}>
            <div className={classes.onItemClickBlock}>
              <Editor code={defaultButtonVideoDetailsPage} />
            </div>
          </Collapse>
          <Button
            className="btn btn-danger mt-3"
            onClick={() => {
            // save all of the changes
              setParams({
                name: 'videoDetailsPage',
                props: {
                  displayButtons,
                }
              });
              setShow(false);
            }}
          >
            Save changes
          </Button>
        </Form>
      </PageConfigModal>
    </>
  )
};

export default VideoDetailsPage;
