import React, { useState } from 'react';
import { Button, Form, Collapse } from 'react-bootstrap';

import Checkbox from '../Checkbox';
import Editor from '../Editor';
import Input from '../Input';
import ExapandableList from '../ExapandableList';

import PageConfigModal from '../PagesConfig/PageConfigModal';

import classes from '../LeftNavMenu/LeftNavMenu.module.scss';

const defaultButtonVideoCollectionPage = `[
  {
    onItemClick: async (e, item) => {
      e.preventDefault();
      console.log(item);
    }
  }
]`;


const VideoCollectionPage = ({
  page,
  setParams,
  activated
}) => {
  const [pageTitle, setPageTitle] = useState(page.props.pageTitle);
  const [onItemClick, setOnItemClick] = useState(page.props.onItemClick);
  const [open, setOpen] = useState({
    options: true,
    mediaType: true,
    htmlContainer: true,
    apiKey: true,
    pageTitle: true,
    callbacks: true,
  });

  const [show, setShow] = useState(false);

  return (
    <>
      <div>
        {activated && (
          <a href="#" onClick={(e) => {
            e.preventDefault();
            setShow(true)
          }}> Edit</a>
        )}
      </div>
      <PageConfigModal show={show} setShow={setShow} pageDisplayName="Video Collection Page">
        <Form>
        <ExapandableList
          heading="Options"
          classes={classes}
          open={open.options}
          onClick={() => setOpen({ ...open, options: !open.options })}
          style={{ borderBottom: 0 }}
        >
          <h6>Title</h6>
          <Input value={pageTitle} onChange={(e) => setPageTitle(e.target.value)} />
        </ExapandableList>
        <Checkbox label="Use onItemClick callback" value="onItemClick" defaultChecked={onItemClick} onChange={(e) => setOnItemClick(e.target.checked)} />
          <Collapse in={onItemClick}>
            <div className={classes.onItemClickBlock}>
              <Editor code={defaultButtonVideoCollectionPage} />
            </div>
          </Collapse>
          <Button
            className="btn btn-danger mt-3"
            onClick={() => {
            // save all of the changes
              setParams({
                name: 'videoCollectionPage',
                props: {
                  pageTitle,
                  onItemClick
                }
              });
              setShow(false);
            }}
          >
            Save changes
          </Button>
        </Form>
      </PageConfigModal>
    </>
  )
};

export default VideoCollectionPage;
