import { getPageConfig } from "./getPageConfigReact";

export const getReactCode = ({ inputs: { apiKey }, page }) => {
  const pageConfig = getPageConfig(page);
  const renderer = `widgetInstance.getPage('${page.name}').${
    page.name === "searchPage" ? "search();" : "render();"
  }`;

  return `
import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';

const SearchWidget = () => {
  const widgetRef = useRef();
  const ${page.name} = ${pageConfig};

  useEffect(() => {
    const widgetConfig = {
      key: '${apiKey}',
      pages: [${page.name}],
      container: widgetRef.current,
    };

    // eslint-disable-next-line no-undef
    if (typeof window === 'object' && window.ShutterstockWidget) {
      // eslint-disable-next-line no-undef
      const widgetInstance = new window.ShutterstockWidget(widgetConfig);
      ${renderer}
      widgetRef.current = widgetInstance;
    }
  }, []);

  return <div ref={widgetRef} />;
};

export default SearchWidget;
`;
};
