import { connect } from 'react-redux';

import VideoLicensingPageConfig from './VideoLicensingPageConfig';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.videoLicensingPage };
};

const mapDispatchToProps = {
  setParams: setPageParams,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(VideoLicensingPageConfig);
