import { connect } from 'react-redux';

import VideoDetailsPageConfig from './VideoDetailsPageConfig';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.videoDetailsPage };
};

const mapDispatchToProps = {
  setParams: setPageParams,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(VideoDetailsPageConfig);
